
.goods {
  position: absolute;
  top: 174px;
  bottom: 46px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  overflow: hidden;
  width: 100%;
}
.goods .menu-wrapper {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 80px;
          flex: 0 0 80px;
  width: 80px;
  background-color: #f3f5f7;
}
.goods .menu-wrapper .menu-item {
  display: table;
  width: 56px;
  height: 54px;
  line-height: 14px;
  padding: 0 12px;
  text-align: center;
}
.goods .menu-wrapper .menu-item.current {
  position: relative;
  margin-top: -1px;
  z-index: 10;
  background-color: #fff;
  font-weight: 700;
}
.goods .menu-wrapper .menu-item.current .text {
  border: none;
}
.goods .menu-wrapper .menu-item .icon {
  display: inline-block;
  width: 12px;
  height: 12px;
  margin-right: 2px;
  background-size: 12px 12px;
  background-repeat: no-repeat;
  vertical-align: top;
}
.goods .menu-wrapper .menu-item .icon.decrease {
  background-image: url(decrease.png);
}
.goods .menu-wrapper .menu-item .icon.discount {
  background-image: url(discount.png);
}
.goods .menu-wrapper .menu-item .icon.special {
  background-image: url(special.png);
}
.goods .menu-wrapper .menu-item .icon.invoice {
  background-image: url(invoice.png);
}
.goods .menu-wrapper .menu-item .icon.guarantee {
  background-image: url(guarantee.png);
}
.goods .menu-wrapper .menu-item .text {
  display: table-cell;
  font-size: 12px;
  width: 56px;
  vertical-align: middle;
  border-bottom: 0.5px solid #E5E9F2;
}
.goods .foods-wrapper {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.goods .foods-wrapper .title {
  padding-left: 14px;
  height: 26px;
  line-height: 26px;
  border-left: 2px solid #d9dde1;
  font-size: 12px;
  color: #93999f;
  background-color: #f3f5f7;
}
.goods .foods-wrapper .food-item {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 18px;
  padding-bottom: 18px;
  border-bottom: 0.5px solid #E5E9F2;
}
.goods .foods-wrapper .food-item:last-child {
  border-bottom: 0;
  margin-bottom: 0;
}
.goods .foods-wrapper .food-item .icon {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 57px;
          flex: 0 0 57px;
  margin-right: 10px;
}
.goods .foods-wrapper .food-item .content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.goods .foods-wrapper .food-item .content .name {
  margin: 2px 0 8px 0;
  height: 14px;
  line-height: 14px;
  font-size: 14px;
  color: #07111b;
}
.goods .foods-wrapper .food-item .content .desc {
  margin-bottom: 8px;
  line-height: 12px;
  font-size: 10px;
  color: #93999f;
}
.goods .foods-wrapper .food-item .content .extra {
  line-height: 10px;
  font-size: 10px;
  color: #93999f;
}
.goods .foods-wrapper .food-item .content .extra .count {
  margin-right: 12px;
}
.goods .foods-wrapper .food-item .content .price {
  font-weight: 700;
  line-height: 24px;
}
.goods .foods-wrapper .food-item .content .price .now {
  margin-right: 8px;
  font-size: 14px;
  color: #f01414;
}
.goods .foods-wrapper .food-item .content .price .old {
  text-decoration: line-through;
  font-size: 10px;
  color: #93999f;
}
.goods .foods-wrapper .food-item .content .cartcontrol-wrapper {
  position: absolute;
  right: 0;
  bottom: 10px;
}



