
.ratingselect .rating-type {
  padding: 18px 0;
  margin: 0 18px;
  border-bottom: 0.5px solid rgba(7, 17, 27, 0.1);
  font-size: 0;
}
.ratingselect .rating-type .block {
  display: inline-block;
  padding: 8px 12px;
  margin-right: 8px;
  border-radius: 1px;
  font-size: 12px;
  line-height: 16px;
  color: #4d555d;
}
.ratingselect .rating-type .block.active {
  color: #fff;
}
.ratingselect .rating-type .block .count {
  font-size: 8px;
  margin-left: 2px;
}
.ratingselect .rating-type .block.positive {
  background-color: rgba(0, 160, 220, 0.2);
}
.ratingselect .rating-type .block.positive.active {
  background-color: #00a0dc;
}
.ratingselect .rating-type .block.negative {
  background-color: rgba(77, 85, 93, 0.2);
}
.ratingselect .rating-type .block.negative.active {
  background-color: #4d555d;
}
.ratingselect .switch {
  padding: 12px 18px;
  line-height: 24px;
  font-size: 0;
  border-bottom: 1px solid rgba(1, 17, 27, 0.1);
  color: #93999f;
}
.ratingselect .switch.on .icon-roundcheckfill {
  color: #00c850;
}
.ratingselect .switch .icon-roundcheckfill {
  display: inline-block;
  vertical-align: top;
  margin-right: 4px;
  font-size: 24px;
}
.ratingselect .switch .text {
  display: inline-block;
  vertical-align: top;
  font-size: 12px;
}



