
.food {
  position: fixed;
  top: 0;
  bottom: 48px;
  left: 0;
  width: 100%;
  background-color: #fff;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  opacity: 1;
}
.food.move-enter-active,
.food.move-leave-active {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
  opacity: 1;
}
.food.move-enter,
.food.move-leave-to {
  opacity: 0;
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}
.food .back {
  position: fixed;
  top: 10px;
  left: 10px;
  background-color: rgba(7, 17, 27, 0.4);
  border-radius: 50%;
}
.food .back .icon-back {
  display: inline-block;
  padding: 6px;
  color: #fff;
  font-size: 26px;
}
.food .img-header {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 100%;
}
.food .img-header img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.food .content {
  position: relative;
  padding: 18px;
}
.food .content .title {
  font-size: 14px;
  line-height: 14px;
  margin-bottom: 8px;
  font-weight: 700;
  color: rbg(7, 17, 27);
}
.food .content .detail {
  font-size: 0;
  line-height: 10px;
  margin-bottom: 18px;
  height: 10px;
}
.food .content .detail .sell-count,
.food .content .detail .rating {
  font-size: 10px;
  color: #93999f;
}
.food .content .detail .sell-count {
  margin-right: 12px;
}
.food .content .price {
  font-weight: 700;
  line-height: 24px;
}
.food .content .price .now {
  margin-right: 8px;
  font-size: 14px;
  color: #f01414;
}
.food .content .price .old {
  text-decoration: line-through;
  font-size: 10px;
  color: #93999f;
}
.food .content .cartcontrol-wrapper {
  position: absolute;
  right: 12px;
  bottom: 12px;
}
.food .content .buy {
  position: absolute;
  right: 18px;
  bottom: 18px;
  z-index: 10;
  height: 24px;
  line-height: 24px;
  padding: 0 12px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 10px;
  border-radius: 12px;
  color: #fff;
  background-color: #00a0dc;
}
.food .content .buy.fade-enter-actice,
.food .content .buy.fade-leave-active {
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  opacity: 1;
}
.food .content .buy.fade-enter,
.food .content .buy.fade-leave-to {
  opacity: 0;
}
.food .info {
  padding: 18px;
}
.food .info .title {
  font-size: 14px;
  margin-bottom: 6px;
  line-height: 14px;
  color: #07111b;
}
.food .info .text {
  font-size: 12px;
  line-height: 24px;
  padding: 0 8px;
  color: #4d555d;
}
.food .rating {
  padding-top: 18px;
}
.food .rating .title {
  font-size: 14px;
  margin-left: 18px;
  line-height: 14px;
  color: #07111b;
}
.food .rating .rating-wrapper {
  padding: 0 18px;
}
.food .rating .rating-wrapper .rating-item {
  position: relative;
  padding: 16px 0;
  border-bottom: 0.5px solid rgba(7, 17, 27, 0.1);
}
.food .rating .rating-wrapper .rating-item .user {
  position: absolute;
  right: 0;
  top: 16px;
  font-size: 0;
  line-height: 12px;
}
.food .rating .rating-wrapper .rating-item .user .name {
  display: inline-block;
  margin-right: 6px;
  vertical-align: top;
  font-size: 10px;
  color: #93999f;
}
.food .rating .rating-wrapper .rating-item .user .avatar {
  border-radius: 50%;
}
.food .rating .rating-wrapper .rating-item .time {
  margin-bottom: 6px;
  line-height: 12px;
  font-size: 10px;
  color: #93999f;
}
.food .rating .rating-wrapper .rating-item .text {
  font-size: 12px;
  line-height: 16px;
  color: #07111b;
}
.food .rating .rating-wrapper .rating-item .text .icon-thumbdown,
.food .rating .rating-wrapper .rating-item .text .icon-thumb_up {
  margin-right: 4px;
  line-height: 16px;
  font-size: 12px;
}
.food .rating .rating-wrapper .rating-item .text .icon-thumb_up {
  color: #00a0dc;
}
.food .rating .rating-wrapper .rating-item .text .icon-thumbdown {
  color: #93999f;
}
.food .rating .rating-wrapper .no-rating {
  padding: 16px 0;
  font-size: 12px;
  color: #93999f;
}



