
@import 'common/fonts/iconfont.css';
@import 'common/less/swiper-3.4.2.min.css';
@import '../static/css/reset.css';
body,
html {
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  font-weight: 400;
}
.clearfix {
  display: inline-block;
}
.clearfixafter {
  display: block;
  content: ".";
  height: 0;
  line-height: 0;
  clear: both;
  visibility: hidden;
}
#app .tab {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 40px;
  line-height: 40px;
  border-bottom: 0.5px solid #E5E9F2;
}
#app .tab .tab-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
}
#app .tab .tab-item > a {
  display: block;
  color: #1F2D3D;
  font-size: 14px;
}
#app .tab .tab-item > a.active {
  color: #20A0FF;
}



