
.cartcontrol {
  font-size: 0;
}
.cartcontrol .cart-decrease {
  display: inline-block;
  padding: 6px;
  opacity: 1;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
.cartcontrol .cart-decrease .inner {
  display: inline-block;
  font-size: 22px;
  line-height: 22px;
  color: #00a0dc;
  -webkit-transition: all .4s ease;
  transition: all .4s ease;
  -webkit-transform: scale(1);
          transform: scale(1);
}
.cartcontrol .cart-decrease.move-enter-active,
.cartcontrol .cart-decrease.move-leave-active {
  -webkit-transition: all .4s ease;
  transition: all .4s ease;
}
.cartcontrol .cart-decrease.move-enter,
.cartcontrol .cart-decrease.move-leave-active {
  opacity: 0;
  -webkit-transform: translateX(24px);
          transform: translateX(24px);
}
.cartcontrol .cart-decrease.move-enter .inner,
.cartcontrol .cart-decrease.move-leave-active .inner {
  -webkit-transform: scale(0);
          transform: scale(0);
}
.cartcontrol .cart-count {
  display: inline-block;
  vertical-align: top;
  width: 12px;
  padding-top: 6px;
  line-height: 22px;
  text-align: center;
  font-size: 10px;
  color: #93999f;
}
.cartcontrol .cart-add {
  display: inline-block;
  padding: 6px;
}
.cartcontrol .cart-add .icon-roundaddfill {
  display: inline-block;
  font-size: 22px;
  line-height: 22px;
  color: #00a0dc;
}



