
.header {
  position: relative;
  color: #fff;
  background-color: rgba(7, 17, 27, 0.5);
  overflow: hidden;
}
.header .content-wrapper {
  position: relative;
  padding: 24px 12px 18px 24px;
  font-size: 0;
  /*并列元素有换行空格产生的空隙，父元素fz=0，子元素要设fz*/
}
.header .content-wrapper .avatar {
  display: inline-block;
  vertical-align: top;
}
.header .content-wrapper .avatar img {
  border-radius: 2px;
}
.header .content-wrapper .content {
  display: inline-block;
  margin-left: 16px;
}
.header .content-wrapper .content .title {
  margin: 2px 0 8px 0;
}
.header .content-wrapper .content .title .brand {
  width: 30px;
  height: 18px;
  display: inline-block;
  background: url(brand.png) no-repeat;
  background-size: 30px 18px;
  vertical-align: top;
  border-radius: 2px;
}
.header .content-wrapper .content .title .name {
  margin-left: 6px;
  font-size: 16px;
  line-height: 18px;
  font-weight: bold;
}
.header .content-wrapper .content .description {
  margin-bottom: 10px;
  line-height: 12px;
  font-size: 12px;
}
.header .content-wrapper .content .support .icon {
  display: inline-block;
  width: 12px;
  height: 12px;
  margin-right: 4px;
  background-size: 12px 12px;
  background-repeat: no-repeat;
  vertical-align: top;
}
.header .content-wrapper .content .support .icon.decrease {
  background-image: url(decrease.png);
}
.header .content-wrapper .content .support .icon.discount {
  background-image: url(discount.png);
}
.header .content-wrapper .content .support .icon.special {
  background-image: url(special.png);
}
.header .content-wrapper .content .support .icon.invoice {
  background-image: url(invoice.png);
}
.header .content-wrapper .content .support .icon.guarantee {
  background-image: url(guarantee.png);
}
.header .content-wrapper .content .support .text {
  line-height: 12px;
  font-size: 10px;
}
.header .content-wrapper .support-count {
  position: absolute;
  right: 12px;
  bottom: 18px;
  padding: 0 8px;
  height: 24px;
  line-height: 24px;
  border-radius: 14px;
  background-color: rgba(0, 0, 0, 0.2);
  text-align: center;
}
.header .content-wrapper .support-count .count {
  font-size: 10px;
  vertical-align: top;
}
.header .content-wrapper .support-count .icon-right {
  font-size: 10px;
  margin-left: 2px;
}
.header .bulletin-wrapper {
  position: relative;
  background-color: rgba(7, 17, 24, 0.2);
  height: 28px;
  line-height: 28px;
  padding: 0 22px 0 12px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.header .bulletin-wrapper .bulletin-title {
  display: inline-block;
  width: 22px;
  height: 12px;
  background: url(bulletin.png) no-repeat;
  background-size: 22px 12px;
}
.header .bulletin-wrapper .bulletin-text {
  vertical-align: top;
  margin: 0 4px;
  font-size: 10px;
}
.header .bulletin-wrapper .icon-right {
  position: absolute;
  right: 12px;
  top: 0px;
  font-size: 10px;
}
.header .background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  -webkit-filter: blur(8px);
          filter: blur(8px);
  overflow: hidden;
}
.header .detail {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  overflow: auto;
  -webkit-backdrop-filter: blur(10px);
  background: rgba(7, 17, 27, 0.8);
  opacity: 1;
}
.header .detail.fade-enter-active,
.header .detail.fade-leave-active {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.header .detail.fade-enter {
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}
.header .detail.fade-leave-to {
  opacity: 0;
  background: rgba(7, 17, 27, 0);
}
.header .detail .detail-wrapper {
  min-height: 100%;
  width: 100%;
}
.header .detail .detail-wrapper .detail-main {
  margin-top: 64px;
  padding-bottom: 64px;
}
.header .detail .detail-wrapper .detail-main .name {
  text-align: center;
  line-height: 16px;
  font-size: 16px;
  font-weight: 700;
}
.header .detail .detail-wrapper .detail-main .star-wrapper {
  margin-top: 18px;
  padding: 2px 0;
  text-align: center;
}
.header .detail .detail-wrapper .detail-main .title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 80%;
  margin: 28px auto 24px auto;
}
.header .detail .detail-wrapper .detail-main .title .line {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  position: relative;
  top: -6px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}
.header .detail .detail-wrapper .detail-main .title .text {
  padding: 0 12px;
  font-size: 14px;
  font-weight: 700;
}
.header .detail .detail-wrapper .detail-main .supports {
  width: 80%;
  margin: 0 auto;
}
.header .detail .detail-wrapper .detail-main .supports .support-item {
  padding: 0 12px;
  margin-bottom: 12px;
  font-size: 0;
}
.header .detail .detail-wrapper .detail-main .supports .support-item:last-child {
  margin-bottom: 0;
}
.header .detail .detail-wrapper .detail-main .supports .support-item .icon {
  display: inline-block;
  width: 16px;
  height: 16px;
  vertical-align: top;
  margin-right: 6px;
  background-size: 16px 16px;
  background-repeat: no-repeat;
}
.header .detail .detail-wrapper .detail-main .supports .support-item .icon.decrease {
  background-image: url(decrease.png);
}
.header .detail .detail-wrapper .detail-main .supports .support-item .icon.discount {
  background-image: url(discount.png);
}
.header .detail .detail-wrapper .detail-main .supports .support-item .icon.special {
  background-image: url(special.png);
}
.header .detail .detail-wrapper .detail-main .supports .support-item .icon.invoice {
  background-image: url(invoice.png);
}
.header .detail .detail-wrapper .detail-main .supports .support-item .icon.guarantee {
  background-image: url(guarantee.png);
}
.header .detail .detail-wrapper .detail-main .supports .support-item .text {
  line-height: 16px;
  font-size: 12px;
}
.header .detail .detail-wrapper .detail-main .bulletin {
  width: 80%;
  margin: 0 auto;
}
.header .detail .detail-wrapper .detail-main .bulletin .content {
  padding: 0 12px;
  font-size: 12px;
  line-height: 24px;
}
.header .detail .detail-close {
  position: relative;
  width: 32px;
  height: 32px;
  margin: -64px auto;
  clear: both;
}
.header .detail .detail-close .icon-close {
  font-size: 30px;
}



